html {
  font-size: 16px;
}
@media screen and (min-width: 320px) {
  html {
    font-size: calc(16px + 6 * ((100vw - 320px) / 680));
  }
}
@media screen and (min-width: 1000px) {
  html {
    font-size: 22px;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #6fbf73;
  color: #f5f3da;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

h1, h2 {
  font-family: 'Courgette', cursive;
}

.App .App-header {
  background-color: #4caf50;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1.5em;
}

.App .App-header h1 {
  font-size: 3em;
  margin: 0;
  padding: 0;
  text-shadow: 0.075em 0.075em 0px rgba(0, 0, 0, 0.6);
}

.App .App-header h2 {
  font-size: 1em;
  margin: 0;
  margin-top: 0.2em;
  padding: 0;
  text-shadow: 0.15em 0.15em 0px rgba(0, 0, 0, 0.6);
}

.App .App-main {
  max-width: 960px;
  margin: auto;
  padding-left: 0.75em;
  padding-right: 0.75em;
}

.giftee-dialog-content {
  text-align: center;
  background-color: #4aa24e;
  padding: 2em 2.5em 1.5em 2.5em !important;
}

.giftee-dialog-content h2 {
  text-shadow: 0.12em 0.12em 0px rgba(0, 0, 0, 0.6);
}

.giftee-dialog-content p {
  color: #101010;
  font-size: 1.2em;
  padding-bottom: 1em;
}

.PersonForm .generate-giftees-container {
  display: flex;
  justify-content: flex-end;
}

.PersonForm form {
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.PersonForm .add-person {
  margin-bottom: 8px;
  margin-left: 14px;
}

.PersonForm .generate-giftees {
  padding-top: 2em;
}
